export default {
	name: 'move-verify',
	data() {
		return {
			x: 0,
			oldx:0,
			isOk: false,
			size: {},
			count:0,
			isMove:false
		};
	},
	mounted() {
		let getSize = (selector) => {
			return new Promise((resolve, reject) => {
				let view = uni.createSelectorQuery().in(this).select(selector);
				view.fields({
					size: true,
				}, (res) => {
					resolve(res.width);
				}).exec();
			});
		}
		this.$nextTick(() => {
			getSize(".on-pathway").then((res1) => {
				this.size.pathway = res1;
				getSize(".on-track").then((res2) => {
					this.size.track = res2;
				});
			})
		})
	},
	methods: {
		/* 滑动中 */
		onMove(e) {
			this.oldx = e.detail.x;
		},
		/* 滑动结束 */
		onEnd() {
			if (this.isOk || this.oldx<1) {
				return;
			}
			
			this.count++;
			this.x = this.oldx;
			if ((this.oldx + 3) > (this.size.pathway - this.size.track)) {
				this.isOk = true;
				this.$emit("result",{flag:true,count:this.count});
			} else {
				this.$nextTick(() => {
					setTimeout(() => {
						this.x = 0;
						this.oldx = 0;
					}, 100);
				});
				this.$emit("result",{flag:false,count:this.count});
			}
		},
		/* 重置 */
		reset(){
			this.x = 0;
			this.oldx = 0;
			this.count = 0;
			this.isOk = false;
		}
	}
}
